// =============================================================================
// Request ID Tracking
// =============================================================================
// Generates and tracks request IDs for request correlation

import { NextRequest, NextResponse } from 'next/server';
import { createLogger, type LogContext } from './logger';

const REQUEST_ID_HEADER = 'x-request-id';
const REQUEST_ID_CONTEXT_KEY = 'requestId';

/**
 * Generate a unique request ID (UUID v4 format)
 * Edge-compatible using Web Crypto API
 */
export function generateRequestId(): string {
  // Generate UUID v4 format: xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx
  const array = new Uint8Array(16);
  crypto.getRandomValues(array);
  
  // Set version (4) and variant bits
  array[6] = (array[6] & 0x0f) | 0x40; // Version 4
  array[8] = (array[8] & 0x3f) | 0x80; // Variant 10
  
  // Convert to UUID string format
  const hex = Array.from(array)
    .map(b => b.toString(16).padStart(2, '0'))
    .join('');
  
  return [
    hex.substring(0, 8),
    hex.substring(8, 12),
    hex.substring(12, 16),
    hex.substring(16, 20),
    hex.substring(20, 32),
  ].join('-');
}

/**
 * Extract request ID from headers or generate a new one
 * 
 * Trusts x-request-id from trusted sources (configurable via TRUSTED_REQUEST_ID_SOURCES env var)
 * Default: trusts all sources (can be restricted to specific IPs/domains)
 */
export function getOrCreateRequestId(request: NextRequest): string {
  const existingId = request.headers.get(REQUEST_ID_HEADER);
  
  if (existingId) {
    // Check if we should trust this request ID
    const trustedSources = process.env.TRUSTED_REQUEST_ID_SOURCES?.split(',') || [];
    const shouldTrust = trustedSources.length === 0 || 
      trustedSources.some(source => {
        const origin = request.headers.get('origin') || '';
        const referer = request.headers.get('referer') || '';
        return origin.includes(source) || referer.includes(source);
      });
    
    if (shouldTrust) {
      return existingId;
    }
  }
  
  return generateRequestId();
}

/**
 * Add request ID to response headers
 */
export function addRequestIdToResponse(
  response: NextResponse,
  requestId: string
): NextResponse {
  response.headers.set(REQUEST_ID_HEADER, requestId);
  return response;
}

/**
 * Get request ID from AsyncLocalStorage (if available)
 */
export function getRequestId(): string | undefined {
  // In Next.js, we'll use headers instead of AsyncLocalStorage
  // This is simpler and works with the serverless model
  return undefined;
}

/**
 * Create a logger with request ID context
 * Uses full Pino logger for structured logging
 * Works in both Node.js and Edge runtime (Pino handles Edge compatibility)
 */
export function createRequestLogger(requestId: string, additionalContext?: Record<string, any>) {
  const context: LogContext = { requestId, ...additionalContext };
  const logger = createLogger(context);
  
  return {
    info: (message: string, meta?: LogContext) => {
      logger.info({ ...context, ...meta }, message);
    },
    warn: (message: string, meta?: LogContext) => {
      logger.warn({ ...context, ...meta }, message);
    },
    error: (message: string, meta?: LogContext) => {
      logger.error({ ...context, ...meta }, message);
    },
    debug: (message: string, meta?: LogContext) => {
      logger.debug({ ...context, ...meta }, message);
    },
  };
}

